<template>
  <div class="social-contribution">
    <SmallModule title="社会贡献">
      <template #content>
        <div class="content-layout">
          <SocialContributionIndicator
            icon="standardCoal"
            label="节约标准煤(吨)"
            :value="socialConIndexData.standardCoal"
            vColor="#F4D038"
          />
          <SocialContributionIndicator
            style="margin-bottom: 0.42rem"
            icon="co2"
            label="二氧化碳减排(吨)"
            vColor="#01B4F1"
            :value="socialConIndexData.carbonDioxide"
          />
          <SocialContributionIndicator
            style="margin-bottom: 0.42rem"
            icon="so2"
            label="二氧化硫减排(吨)"
            vColor="#06E3F1"
            :value="socialConIndexData.sulfurDioxide"
          />
          <SocialContributionIndicator
            icon="treePlanting"
            label="累计等效植树(万棵)"
            vColor="#00BA24"
            :value="socialConIndexData.trees"
          />
        </div>
      </template>
    </SmallModule>
  </div>
</template>

<script lang="ts" setup>
  import SmallModule from '/@/components/Framework/OperationScreen/SmallModule.vue';
  import SocialContributionIndicator from '/@/components/Framework/OperationScreen/SocialContributionIndicator.vue';
  import { reactive, UnwrapRef, onMounted } from 'vue';
  import { getSocialConStatData } from './data';
  import { assign } from 'lodash-es';

  //搜索参数
  const searchInfo: UnwrapRef<any> = reactive({});
  const socialConIndexData: UnwrapRef<any> = reactive({});

  interface Props {
    overviewSearchInfo?: {};
  }
  const props = withDefaults(defineProps<Props>(), {});
  onMounted(() => {
    assign(searchInfo, props.overviewSearchInfo);
    querySocialConStatData();
  });

  const querySocialConStatData = async () => {
    let data = await getSocialConStatData(searchInfo);
    if (data.result) {
      assign(socialConIndexData, data.result);
    }
  };
</script>

<style lang="less" scoped>
  .social-contribution {
    width: 100%;
  }

  .content-layout {
    display: flex;
    flex-wrap: wrap;
    place-content: center space-between;
    height: 2.69rem;
    padding: 0 0.14rem;
  }
</style>
